html,body{
    background: #ddd;
    padding: 0.5em;
}
.page_li{
    list-style: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    background-color: #EEE;
    padding: 10px 0;
    border-radius: 4px;
    text-align: center;
    margin: 10px auto;
    position: relative;
    left: 0;
}
.page_li:hover{
    left: 1px;
    background: #2abb9c;
}
.page_li:hover a{
    color: #fff;
}
.content{
    display: flex;
    justify-items: center;
    align-items: center;
}
.page_div{
    justify-items: center;
    align-items: center;
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}
.page_li a{
    text-decoration: none;
    color: #000;
    display: inline-block;
    width: 100%;
    height: 100%;
}
.page_ul{
    text-align: center;
    margin: 0 auto;
    padding-left: 0;
    padding: 0 5%;
}
.page_h2{
    text-align: center;
    width: 100%;
}
.footer{
    display: flex;
}
.footer span{
    flex: 1;
    text-align: center;
    font-size: 12px;
    cursor: pointer;
}
.footer span:active{
    color: #2abb9c;
}